import { yupResolver } from '@hookform/resolvers/yup'
import SaveIcon from '@mui/icons-material/Save'
import LoadingButton from '@mui/lab/LoadingButton'
import {
	Box,
	FormControl,
	InputAdornment,
	InputLabel,
	MenuItem,
	Select,
	Stack,
	TextField,
	Typography,
} from '@mui/material'
import { DatePicker } from '@mui/x-date-pickers'
import { FormApi } from 'API/form'
import { useEffect, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useNavigate } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getWebs } from 'redux/slices/CMS/Web'
import { getGroups } from 'redux/slices/Group'
import { UIAction } from 'redux/slices/UI'
import * as yup from 'yup'

interface FormInput {
	webId: number
	name: string
	startDate?: Date
	endDate?: Date
}
export default function CreateForm() {
	const dispatch = useAppDispatch()
	const nav = useNavigate()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const groups = useAppSelector((s) => s.Group.list)
	const groupStatus = useAppSelector((s) => s.Group.status)
	const [loading, setLoading] = useState(false)
	const list = useAppSelector((s) => s.Form.list)
	const webs = useAppSelector((s) => s.CMS.Web.list)
	const webStatus = useAppSelector((s) => s.CMS.Web.status)
	const [createdId, setCreatedId] = useState(0)
	const schema = yup.object({
		webId: yup.number().required().min(1),
		name: yup.string().required(),
	})
	const {
		register,
		handleSubmit,
		setValue,
		getValues,
		setError,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			webId: webs[0]?.id,
			name: '',
		},
	})
	useEffect(() => {
		if (webStatus === 'init') dispatch(getWebs())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	useEffect(() => {
		if (groupStatus === 'init') dispatch(getGroups())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	useEffect(() => {
		if (list.find((f) => f.id === createdId)) {
			nav(`/form/${createdId}`)
		}
	}, [createdId, list, nav])

	if (!tokenInfo) return null
	const group = groups.find((f) => f.id === tokenInfo.groupId)
	if (!group) return null

	const onSubmit: SubmitHandler<FormInput> = async ({ webId, name, startDate, endDate }) => {
		try {
			setLoading(true)
			const { id, message, error } = await FormApi.create({ webId, name: `[${group.name}]${name}`, startDate, endDate })
			dispatch(
				UIAction.addGlobalMessage({
					message,
					type: error ? 'error' : 'success',
				})
			)
			setCreatedId(id)
		} catch (error) {
		} finally {
			setLoading(false)
		}
	}

	return (
		<Stack>
			<Stack direction="row" alignItems="center" justifyContent="space-between">
				<Typography
					color="primary.dark"
					sx={{
						fontWeight: 600,
					}}
				>
					フォーム情報定義
				</Typography>
			</Stack>
			<Stack
				gap={2}
				my={4}
				maxWidth="sm"
				component="form"
				noValidate
				autoComplete="off"
				onSubmit={handleSubmit(onSubmit)}
			>
				<FormControl fullWidth size="small" required>
					<InputLabel>使用ウェブサイト</InputLabel>
					<Select
						label="使用ウェブサイト"
						value={getValues('webId')}
						renderValue={(selected) => {
							if (selected === 0) {
								return (
									<Typography variant="body2" component="em" sx={{ color: '#aaa' }}>
										選択してください
									</Typography>
								)
							}
							const web = webs.find((f) => f.id === selected)
							return web?.name || '--'
						}}
						onChange={(event) => {
							setValue('webId', Number(event.target.value), { shouldValidate: true })
						}}
						error={'webId' in errors}
					>
						{webs.map((v) => {
							return (
								<MenuItem key={v.id} value={v.id}>
									{v.name}
								</MenuItem>
							)
						})}
					</Select>
				</FormControl>
				<TextField
					variant="outlined"
					required
					margin="dense"
					autoFocus
					fullWidth
					size="small"
					InputProps={{
						startAdornment: <InputAdornment position="start">[{group.name}]</InputAdornment>,
					}}
					label={'フォーム名'}
					{...register('name')}
					error={'name' in errors}
				/>
				<DatePicker
					label="開始日"
					inputFormat="yyyy年MM月dd日"
					value={getValues('startDate') || null}
					onChange={(date) => {
						if (date) {
							if (date instanceof Date && !isNaN(date.valueOf())) {
								setValue('startDate', date, {
									shouldValidate: true,
								})
								const ed = getValues('endDate')
								if (ed && ed < date) {
									setValue('endDate', date, {
										shouldValidate: true,
									})
								}
							}
						}
					}}
					onError={(error) => {
						setError('startDate', {
							message: error?.toString(),
						})
					}}
					renderInput={(params) => <TextField {...params} size="small" />}
				/>
				<DatePicker
					label="終了日"
					inputFormat="yyyy年MM月dd日"
					value={getValues('endDate') || null}
					onChange={(date) => {
						if (date) {
							if (date instanceof Date && !isNaN(date.valueOf())) {
								setValue('endDate', date, {
									shouldValidate: true,
								})
							}
						}
					}}
					onError={(error) => {
						setError('endDate', {
							message: error?.toString(),
						})
					}}
					minDate={getValues('startDate')}
					renderInput={(params) => <TextField {...params} size="small" />}
				/>
				<Box>
					<LoadingButton
						variant="outlined"
						disabled={Object.keys(errors).length > 0}
						loading={loading}
						loadingPosition="start"
						startIcon={<SaveIcon />}
						type="submit"
					>
						確定する
					</LoadingButton>
				</Box>
			</Stack>
		</Stack>
	)
}
